import React, { useEffect, useState, useRef } from "react";
import Headeradmin from "../../components/Admin/HeaderAdmin";
import NavAdmin from "../../components/Admin/NavAdmin";
import axios from "axios";
import moment from "moment";
import { useNavigate } from "react-router-dom";
import Chart from "chart.js/auto"; // Import Chart.js
export default function Usersgdpr() {
  const navigate = useNavigate();
  const canvasRef3 = useRef(null);
  const [totalCookies, settotalCookies] = useState([]);
  const [lastdate, setlastdate] = useState("");
  const [datachartYearDoug, setdatachartYearDoug] = useState([]);
  const [datachartYearlDoug, setdatachartYearlDoug] = useState([]);
  const apiUrl = "https://backend.amourette.no/api/admin/";
  useEffect(() => {
    // Check if the username key exists in session storage
    const storedUsername = localStorage.getItem("adminLogin");
    const userLogin = JSON.parse(storedUsername);
    console.log(userLogin);
    if (userLogin === null) {
      navigate("/admin/login");
    }
  }, []);

  useEffect(() => {
    gettotalCookies();
  }, []);
  const gettotalCookies = async () => {
    let formData = {
      slug: "",
    };
    try {
      const res = await axios.post(apiUrl + "gettotalCookies", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      settotalCookies(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  useEffect(() => {
    getlastdate();
  }, []);
  const getlastdate = async () => {
    let formData = {
      slug: "",
    };
    try {
      const res = await axios.post(apiUrl + "getlastdate", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      if (respo.length > 0) {
        var datelast = respo;
        var d = moment(datelast[0].date).format("MMM DD, YYYY");
        setlastdate(d);
      }
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  useEffect(() => {
    getuserchartCookies();
  }, []);
  const getuserchartCookies = async () => {
    let formData = {
      user_id: "",
    };
    try {
      const res = await axios.post(apiUrl + "getuserchartCookies", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });

      // Extract datasets and labels from the response
      const { labels, datasets } = res.data.result;

      console.log("Chart Data:", res.data);
      console.log("Labels:", labels);
      console.log("Datasets:", datasets);

      // Set state
      setdatachartYearlDoug(labels);
      setdatachartYearDoug(datasets);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
        console.error("Response Error:", err.response);
      } else if (err.request) {
        console.error("Request Error:", err.request);
      } else {
        console.error("Error Message:", err.message);
      }
    }
  };
  useEffect(() => {
    const script66 = document.createElement("script");
    script66.src = "https://cdn.jsdelivr.net/npm/chart.js";
    script66.async = true;
    document.body.appendChild(script66);

    script66.onload = () => {};

    return () => {
      document.body.removeChild(script66);

      if (window.myChart3) {
        window.myChart3.destroy();
      }
    };
  }, [datachartYearlDoug, datachartYearDoug]);
  useEffect(() => {
    if (datachartYearDoug && datachartYearlDoug) {
      const ctx3 = canvasRef3.current.getContext("2d");
      // const ctx3 = document.getElementById("myChart3").getContext("2d");

      // Create a Doughnut Chart
      window.myChart3 = new Chart(ctx3, {
        type: "doughnut",
        data: {
          labels: datachartYearlDoug,
          datasets: datachartYearDoug, // Datasets dynamically set
        },
        options: {
          responsive: true,
        },
      });
    }
  }, [datachartYearDoug, datachartYearlDoug]);
  const [isOpen, setIsOpen] = useState(false);

  const toggleNavAdmin = () => {
    setIsOpen(!isOpen); // Toggle the state between true and false
  };
  return (
    <>
      <div class="position-relative bg-white d-flex p-0">
        <Headeradmin isOpen={isOpen} />
        <div className={`content ${isOpen ? "open" : ""}`}>
          <NavAdmin onClick={toggleNavAdmin} />

          <div className="container-fluid pt-4 px-4">
            <div className="row ">
              <div className="col-sm-6 col-xl-6 bg-light rounded d-flex align-items-start justify-content-start pt-4">
                <div className="row align-items-start cookies w-100">
                  <h6>Cookies Summary</h6>
                  <div className="col-md-6">
                    <div className="row">
                      <div className="col-md-6">
                        <div className="d-flex flex-column justify-content-center align-items-center">
                          <i className="fa fa-cookie fa-3x text-primary"></i>
                          <div className="d-flex flex-column gap-2 justify-content-center align-items-center text-center pt-3">
                            <h6 className="m-0">{totalCookies.length}</h6>
                            <p className="m-0">Total Cookies</p>
                          </div>
                        </div>
                      </div>
                      <div className="col-md-6">
                        <div className="d-flex flex-column justify-content-center align-items-center">
                          <i className="fa fa-th-large fa-3x text-primary"></i>
                          <div className="d-flex flex-column gap-2 justify-content-center align-items-center text-center pt-3">
                            <h6 className="m-0">0</h6>
                            <p className="m-0">Category</p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="d-flex flex-column gap-3">
                      <div className="d-flex align-items-center gap-3">
                        <i className="fas fa-calendar-check fa-3x text-primary"></i>
                        <div className="d-flex flex-column gap-1 ">
                          <p className="m-0">last Date Scan</p>
                          <h6 className="m-0">{lastdate}</h6>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <div className="col-sm-12 col-xl-6">
                <div className="bg-light text-center rounded p-4">
                  <div className="d-flex align-items-center justify-content-between mb-4">
                    <h6 className="mb-0">Consent Trends</h6>
                    {/* <a href="">Show All</a> */}
                  </div>
                  <canvas ref={canvasRef3} id="doughnut-chart"></canvas>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
